.knowledge-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .knowledge-item{
      margin: 20px;
      display: flex;
      align-items: center;
      gap: 10px;
      border-radius: 8px;
      border: 1px solid #e0e0e0;
      padding: 15px;
      cursor: pointer;
      width: 225px;
      transition: all 0.3s ease-in-out;
      h4{
        font-size: 1.2rem;
        font-weight: 600;
        letter-spacing: 2px;
        color: #846e6e;
        padding-left: 5px;
      }
    }
}
